@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;0,900;1,400&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Lato', sans-serif;

}
.container{
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(#ffe259, #ffa751);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
}
h1{
  margin-bottom: 50px;
  font-size: 60px;
  color: rgb(248, 244, 244);
  position: absolute;
  top: 100px;
  text-transform: uppercase;
  text-shadow: 2px 2px 5px rgba(78, 78, 78, 0.3)
}
.todo-box{
  width: 500px;
  background-image: linear-gradient(to right bottom, #ffe259, #ffa751  );
  height: 60vh;
  padding: 20px;
  font-size: 20px;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  
}
.todo-box input{
  width: 80%;
  padding: 15px;
  border: none;
  outline: none;
  border-radius: 3px;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
}

.todo-box button{
  margin-left: 30px;
  padding: 13px;
  border: none;
  outline: none;
  border-radius: 3px;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
  font-weight: bold;
  text-transform: uppercase;
  transition: 0.2s ease all;
}
.todo-box button:hover{
  background-color: rgb(184, 137, 49);
  color: #fff;
  font-weight: bold;
}
.list-items{
  padding: 30px 0 ;
  
}
.list-items li{
  list-style: none;
}

.items {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0 0 10px 0;
  text-align: center;
}
.items-btn button{
  font-size: 12px;
  border-radius: 10%;
  /* width: 50px;
  height: 50px; */
  font-weight: normal;
  text-align: center;
}